import React, { useEffect } from 'react';
import { QueryClientProvider } from '@tanstack/react-query';
import queryClient from '@root/src/lib/core/react-query/queryClient';
import withSuspense from '@src/shared/hoc/withSuspense';
import withErrorBoundary from '@src/shared/hoc/withErrorBoundary';
import { useNav } from '@src/lib/core/nav/nav.store';
import { NavigationRegistry } from '@src/lib/core/nav/nav.registry';
import { useUsersStore } from '@src/lib/core/auth/auth.store';
import SignIn from '@src/lib/ui/features/SignIn';
import NotificationsSubscribe from '@root/src/realtime/notificationsSubscribe';

const NewTab = () => {
  const { selection, load } = useNav(s => s);
  const Component = NavigationRegistry[selection?.key || 'dashboard'];
  const { user, getSession } = useUsersStore(state => state);

  useEffect(() => {
    load();
    getSession();
  }, [load]);

  return user ? <Component /> : <SignIn />;
};

const NewTabWithProviders = () => (
  <QueryClientProvider client={queryClient}>
    <NewTab />
    <NotificationsSubscribe />
  </QueryClientProvider>
);

export default withErrorBoundary(withSuspense(NewTabWithProviders, <div>Loading...</div>), <div>Error Occur</div>);
